.checkbox {
  user-select: none;
}

.checkbox > [type="checkbox"] {
  cursor: pointer;
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 1.5em;
  height: 1.5em;
  top: 0.25em;
  padding: 0;
  margin: 0;
  visibility: hidden;
}

.checkbox > [type="checkbox"]:disabled {
  cursor: not-allowed;
}

.checkbox > [type="checkbox"]:disabled:before {
  opacity: 0.75;
  background-color: #b5b5b5 !important;
  border-color: #b5b5b5 !important;
}

.checkbox > [type="checkbox"]:disabled:checked:before {
  background-color: #b5b5b5 !important;
  border-color: #b5b5b5 !important;
}

.checkbox > [type="checkbox"]:before {
  visibility: visible;
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  border: 2px solid #dbdbdb;
  border-radius: 2px;
  transition: all 0.25s ease-out;
}

.checkbox > [type="checkbox"]:after {
  visibility: visible;
  display: none;
  content: '';
  position: absolute;
  left: 0.5em;
  top: 10%;
  height: 66%;
  width: 33%;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(30deg);
  -ms-transform: rotate(30deg);
  transform: rotate(30deg);
}

.checkbox > [type="checkbox"]:checked:before {
  background-color: #b5b5b5;
  border-color: #b5b5b5;
}

.checkbox > [type="checkbox"]:checked:after {
  display: block;
}

.checkbox:hover > [type="checkbox"]:not(:disabled):before {
  border-color: rgba(0, 0, 0, 0.5);
}

.checkbox.is-white > [type="checkbox"]:checked:before {
  background-color: white;
  border-color: #fcfcfc;
}

.checkbox.is-black > [type="checkbox"]:checked:before {
  background-color: #0a0a0a;
  border-color: #080808;
}

.checkbox.is-light > [type="checkbox"]:checked:before {
  background-color: whitesmoke;
  border-color: #f2f2f2;
}

.checkbox.is-dark > [type="checkbox"]:checked:before {
  background-color: #363636;
  border-color: #333333;
}

.checkbox.is-primary > [type="checkbox"]:checked:before {
  background-color: #00d1b2;
  border-color: #00ccad;
}

.checkbox.is-link > [type="checkbox"]:checked:before {
  background-color: #3273dc;
  border-color: #2e70dc;
}

.checkbox.is-info > [type="checkbox"]:checked:before {
  background-color: #209cee;
  border-color: #1b9aee;
}

.checkbox.is-success > [type="checkbox"]:checked:before {
  background-color: #23d160;
  border-color: #23cd5e;
}

.checkbox.is-warning > [type="checkbox"]:checked:before {
  background-color: #ffdd57;
  border-color: #ffdc52;
}

.checkbox.is-danger > [type="checkbox"]:checked:before {
  background-color: #ff3860;
  border-color: #ff335c;
}

